import { CardHeader,CardMedia,Card } from "@mui/material"

const Tarjeta = (props) => {
    return (
        <Card  variant="outlined" className="tarjeta">
            <CardMedia component="img" image='https://api.lorem.space/image/book?w=150&amp;amp;amp;amp;h=220' />
            <CardHeader title={props.nombre} className="titulo_tarjeta"/>
        </Card>
    )
}
export default Tarjeta