import './Home.css';
import React from "react";
import {Grid} from "@mui/material";
import {Link} from "react-router-dom";
import Tarjeta from "../../components/ejercicios/Tarjeta"
const Home = () => {
    return (
    <Grid container mt={4} mb={4} spacing={5}>
        <Grid item mt={2} mb={2} md={4} lg={3} xs={6}>
            <Link to="/ejercicio/1">
                <Tarjeta nombre="Ejercicio 1"/>
            </Link>
        </Grid>
    
        <Grid item mt={2} mb={2} md={4} lg={3} xs={6}>
            <Link to="/ejercicio/2">
                <Tarjeta nombre="Ejercicio 2" />
            </Link>
        </Grid>
    
        <Grid item mt={2} mb={2} md={4} lg={3} xs={6}>
            <Link to="/ejercicio/3" >
                <Tarjeta nombre="Ejercicio 3" />
            </Link>
        </Grid>
    
        <Grid item mt={2} mb={2} md={4} lg={3} xs={6}>
            <Link to="/ejercicio/4">
                <Tarjeta nombre="Ejercicio 4" />
            </Link>
        </Grid>
    
        <Grid item mt={2} mb={2} md={4} lg={3} xs={6}>
            <Link to="/ejercicio/5">
                <Tarjeta nombre="Ejercicio 5" />
            </Link>
        </Grid>
    </Grid>
    )
}
export default Home