import { useParams } from 'react-router-dom';
import {Grid,FormControl,FormLabel,RadioGroup,FormControlLabel,Radio} from '@mui/material'
import Swal from "sweetalert2"
import "./Detalle.css"
const Detalle = () => {
    const { nro_ej } = useParams()
    const handleChangeResp = (e) => {
        const r = e.target.value
        if(r == "b"){
            Swal.fire({
                title:'Exito!',
                text:'Respuesta correcta',
                icon:'success'
            })
        }else{
            Swal.fire({
                title:"Error!",
                text:"Respusta incorrecta",
                icon:"error"
            })
        }
    }
    return (
    <>
        <Grid container>
            <Grid item mt={5} mb={5} xs={12}>
                <h2>Ejercicio Nro {nro_ej}</h2>
                <p>
                    La gallina Clotilde vivía en un pequeño corral junto con otras siete gallinas y el gallo Corindón. Clotilde era la gallina más hermosa, la que ponía los huevos más grandes y la preferida de Corindón por su ágil vuelo.
                </p>
                <p>
                    En el centro del corral había un cerezo muy alto que al  llegar la primavera se llenaba de grandes y sabrosas cerezas que Clotilde picoteaba alcanzando con su ágil vuelo las ramas más bajas cargadas del rojo fruto.
                </p>
                <p>
                    Las otras gallinas, incapaces de semejante vuelo, tenían que conformarse con las cerezas que Clotilde les tiraba o con las que se caían sacudidas por el viento.
                </p>
                <p>
                    Aburrida en el corral, un día de verano, agotadas ya la cerezas, la hermosa Clotilde emprendió un nuevo vuelo hasta el cerezo y, ascendiendo de rama en rama, se encaramó en lo más alto de su copa.
                </p>
                <p>
                    Desde allí se divisaban inmensos campos llenos de apetitosos granos de trigo, prados habitados por gusanos y lombrices relucientes, y  charcas repletas de insectos.
                </p>
                <p>
                    Desde su atalaya, la gallina observó de nuevo su reducido corral, recordó la escasa comida de todos los días y, sin pensárselo dos veces, emprendió un largo vuelo hacia la libertad y hacia un mundo nuevo y desconocido
                </p>
                <p>
                    El tiempo transcurrió y de Clotilde nunca más se volvió a tener noticia cierta. Hay quienes aseguran que vive con los conejos en los campos de trigo, otros que anda por las charcas con los patos salvajes y aún hay quien afirma que emigró con las cigüeñas a la llegada del invierno.
                </p>
                
            </Grid>
        </Grid>
        <Grid container mt={4} mb={10}>
            <Grid item md={6} sm={12} mb={3}>
                <FormControl>
                    <FormLabel id="demo-row-radio-buttons-group-label">1. Cuantas gallinas habia en el corral ?</FormLabel>
                    <RadioGroup
                        row
                        aria-labelledby="demo-row-radio-buttons-group-label"
                        name="row-radio-buttons-group"
                        onChange={handleChangeResp}>
                        <FormControlLabel value="a" control={<Radio />} label="Siete" />
                        <FormControlLabel value="b" control={<Radio />} label="Ocho" />
                        <FormControlLabel value="c" control={<Radio />} label="Nueve" />
                        
                    </RadioGroup>
                </FormControl>
            </Grid>
            <Grid item md={6} sm={12} mb={3}>
                <FormControl>
                    <FormLabel id="demo-row-radio-buttons-group-label">2. ¿Por qué Clotilde es la preferida de Corindón?</FormLabel>
                    <RadioGroup
                        row
                        aria-labelledby="demo-row-radio-buttons-group-label"
                        name="row-radio-buttons-group"
                        onChange={handleChangeResp}
                    >
                        <FormControlLabel value="b" control={<Radio />} label="Por su ágil vuelo " />
                        <FormControlLabel value="a" control={<Radio />} label="Por ser la más hermosa" />
                        <FormControlLabel value="c" control={<Radio />} label="Por poner los huevos más grandes" />
                        
                    </RadioGroup>
                </FormControl>
            </Grid>
            <Grid item md={6} sm={12} mb={3}>
                <FormControl>
                    <FormLabel id="demo-row-radio-buttons-group-label">3. ¿Qué había en el centro del corral?</FormLabel>
                    <RadioGroup
                        row
                        aria-labelledby="demo-row-radio-buttons-group-label"
                        name="row-radio-buttons-group"
                        onChange={handleChangeResp}
                    >
                        <FormControlLabel value="a" control={<Radio />} label="Un gallinero" />
                        <FormControlLabel value="c" control={<Radio />} label="Un pozo" />
                        <FormControlLabel value="b" control={<Radio />} label="Un árbol" />
                        
                    </RadioGroup>
                </FormControl>
            </Grid>
            <Grid item md={6} sm={12} mb={3}>
                <FormControl>
                    <FormLabel id="demo-row-radio-buttons-group-label">4. ¿Qué comía Clotilde?</FormLabel>
                    <RadioGroup
                        row
                        aria-labelledby="demo-row-radio-buttons-group-label"
                        name="row-radio-buttons-group"
                        onChange={handleChangeResp}
                    >
                        <FormControlLabel value="a" control={<Radio />} label="Manzanas" />
                        <FormControlLabel value="b" control={<Radio />} label="Cerezas" />
                        <FormControlLabel value="c" control={<Radio />} label="Ciruelas" />
                        
                    </RadioGroup>
                </FormControl>
            </Grid>
            <Grid item md={6} sm={12} mb={3}>
                <FormControl>
                    <FormLabel id="demo-row-radio-buttons-group-label">5. ¿Qué comían las otras gallinas?</FormLabel>
                    <RadioGroup
                        row
                        aria-labelledby="demo-row-radio-buttons-group-label"
                        name="row-radio-buttons-group"
                        onChange={handleChangeResp}
                    >
                        <FormControlLabel value="b" control={<Radio />} label="Las cerezas que caían" />
                        <FormControlLabel value="a" control={<Radio />} label="Comían cerezas en el árbol" />
                        <FormControlLabel value="c" control={<Radio />} label="No comían cerezas" />
                        
                    </RadioGroup>
                </FormControl>
            </Grid>
            <Grid item md={6} sm={12} mb={3}>
                <FormControl>
                    <FormLabel id="demo-row-radio-buttons-group-label">6. Cuando Clotilde se subió al árbol por última vez era...</FormLabel>
                    <RadioGroup
                        row
                        aria-labelledby="demo-row-radio-buttons-group-label"
                        name="row-radio-buttons-group"
                        onChange={handleChangeResp}
                    >
                        <FormControlLabel value="a" control={<Radio />} label="Primavera " />
                        <FormControlLabel value="c" control={<Radio />} label="Invierno" />
                        <FormControlLabel value="b" control={<Radio />} label="Verano" />
                        
                    </RadioGroup>
                </FormControl>
            </Grid>
            <Grid item md={6} sm={12} mb={3}>
                <FormControl>
                    <FormLabel id="demo-row-radio-buttons-group-label">7. Desde la parte más alta del cerezo se divisaban...</FormLabel>
                    <RadioGroup
                        row
                        aria-labelledby="demo-row-radio-buttons-group-label"
                        name="row-radio-buttons-group"
                        onChange={handleChangeResp}
                    >
                        <FormControlLabel value="a" control={<Radio />} label="Prados, charcas y campos de trigo" />
                        <FormControlLabel value="b" control={<Radio />} label="Campos de trigo, bosques y charcas" />
                        <FormControlLabel value="c" control={<Radio />} label="Prados, campos de trigo y lagos" />
                        
                    </RadioGroup>
                </FormControl>
            </Grid>
            <Grid item md={6} sm={12} mb={3}>
                <FormControl>
                    <FormLabel id="demo-row-radio-buttons-group-label">8. ¿Qué deseaba comer Clotilde?</FormLabel>
                    <RadioGroup
                        row
                        aria-labelledby="demo-row-radio-buttons-group-label"
                        name="row-radio-buttons-group"
                        onChange={handleChangeResp}
                    >
                        <FormControlLabel value="a" control={<Radio />} label="Granos de trigo, insectos y ranas" />
                        <FormControlLabel value="b" control={<Radio />} label="Granos de trigo, lombrices e insectos" />
                        <FormControlLabel value="c" control={<Radio />} label="Granos de trigo, hierba y gusanos" />
                    </RadioGroup>
                </FormControl>
            </Grid>
            <Grid item md={6} sm={12} mb={3}>
                <FormControl>
                    <FormLabel id="demo-row-radio-buttons-group-label">9. ¿A dónde quería que la llevase su vuelo?</FormLabel>
                    <RadioGroup
                        row
                        aria-labelledby="demo-row-radio-buttons-group-label"
                        name="row-radio-buttons-group"
                        onChange={handleChangeResp}
                    >
                        <FormControlLabel value="a" control={<Radio />} label="Granos de trigo, insectos y ranas" />
                        <FormControlLabel value="b" control={<Radio />} label="Granos de trigo, lombrices e insectos" />
                        <FormControlLabel value="c" control={<Radio />} label="Granos de trigo, hierba y gusanos" />
                    </RadioGroup>
                </FormControl>
            </Grid>
            <Grid item md={6} sm={12} mb={3}>
                <FormControl>
                    <FormLabel id="demo-row-radio-buttons-group-label">10. ¿Dónde estará ahora Clotilde?</FormLabel>
                    <RadioGroup
                        row
                        aria-labelledby="demo-row-radio-buttons-group-label"
                        name="row-radio-buttons-group"
                        onChange={handleChangeResp}
                    >
                        <FormControlLabel value="a" control={<Radio />} label="En los campos de trigo" />
                        <FormControlLabel value="c" control={<Radio />} label="Emigró con las cigüeñas " />
                        <FormControlLabel value="b" control={<Radio />} label="Nadie lo sabe con certeza" />
                    </RadioGroup>
                </FormControl>
            </Grid>
        </Grid>
    </>
    )
}
export default Detalle